@import "../../index.scss";

@media only screen and (min-width: 850px) {

  .staticContent {
    position: fixed;
    bottom: 20%;
    top: 20%;
  }

  .anchorStyle {
    display: grid;
    visibility: visible;
    opacity: .8;
    transition: visibility 0s, opacity 0.5s linear;
  }

  .anchorStyleNone {
    display: grid;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
  }

  .iconsStyleMedia {
    height: 2.5em;
    width: 2.5em;
    margin: 1.8em 1em 0em 1em;
    fill: $baseColor;
    transition: all .3s ease-in-out;
  }

  .iconsStyleMedia:hover {
    fill: $hoverColor;
    transform: scale(1.2);
  }
}

@media only screen and (max-width: 849px) {
  .staticContent {
    background-color: $baseColor;
    padding-bottom: 0.5em;
  }

  .iconsStyleMedia {
    height: 2em;
    width: 2em;
    margin: 1em 0.6em 0em 0.6em;
    fill: white;
    transition: all .3s ease-in-out;
  }

  .iconsStyleMedia:hover {
    fill: $hoverColor;
    transform: scale(1.2);
  }
}