@import "../../index.scss";

@media only screen and (min-width: 850px) {
    .contactContainer {
        height: 25em;
        background-color: $baseColor;
        display: grid;
        grid-template-columns: 60% 40%;
        grid-template-rows: 80% 20%;
        grid-template-areas:
            "textContact infoContact"
            "mediaContainer mediaContainer";
    }

    .mediaContainer {
        grid-area: mediaContainer;
    }

    .rightsContainer {
        text-align: center;
        color: white;
    }

    .imgContactContainer {
        text-align: center;
        margin: 1em;
    }

    .imgContactStyle {
        border-radius: 10em;
        height: 10em;
        margin-top: 1em;
    }

    .textConteiner {
        grid-area: textContact;
        color: white;
        text-align: left;
        padding: 2em 0em 2em 3em;
    }

    .photoLegend {
        grid-area: infoContact;
        color: white;
        text-align: left;
        padding: 2em 0em 2em 2em;
    }


    .titleContact {
        font-size: 3em;
        margin: 1em 0em .5em 0em;
    }

    .textContact {
        font-size: 2em;
        margin: 0% 0% 1em 0%;
    }

    .contactStyleMedia {
        height: 2.5em;
        width: 2.5em;
        margin: 1.8em 1em 0em 1em;
        fill: white;
        transition: all .3s ease-in-out;
    }

    .contactStyleMedia:hover {
        fill: $hoverColor;
        transform: scale(1.2);
    }
}

@media only screen and (max-width: 849px) {
    .contactContainer {
        height: 25em;
        background-color: $baseColor;
        display: grid;
        grid-template-rows: auto auto;
        grid-template-areas:
            "textContact"
            "infoContact";
    }

    .mediaContainer {
        display: none;
    }

    .rightsContainer {
        text-align: center;
        color: white;
    }

    .imgContactContainer {
        text-align: center;
        margin: 1em;
    }

    .imgContactStyle {
        border-radius: 10em;
        height: 10em;
    }

    .textConteiner {
        grid-area: textContact;
        color: white;
        text-align: center;
    }

    .photoLegend {
        grid-area: infoContact;
        color: white;
        text-align: left;
    }


    .titleContact {
        font-size: 1.5em;
        margin: 1em 0em .5em 0em;
    }

    .textContact {
        font-size: 1em;
        margin: 0% 0% 1em 0%;
    }
}