.hobbiesContainer {
  margin: 3em 0em 7em 0em;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
  background-color: white;

  display: grid;
  grid-template-rows: 60% 40%;
  grid-template-areas:
    "img"
    "description";
}

@keyframes myAirplane {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  5% {
    -webkit-transform: rotate(-18deg) translateX(15px) translateY(15px);
    transform: rotate(-18deg) translateX(15px) translateY(15px);
  }

  25% {
    -webkit-transform: rotate(-90deg) translateX(75px) translateY(75px);
    transform: rotate(-90deg) translateX(75px) translateY(75px);
  }

  50% {
    -webkit-transform: rotate(-180deg) translateX(0px) translateY(150px);
    transform: rotate(-180deg) translateX(0px) translateY(150px);
  }

  75% {
    -webkit-transform: rotate(-270deg) translateX(-75px) translateY(75px);
    transform: rotate(-270deg) translateX(-75px) translateY(75px);
  }

  95% {
    -webkit-transform: rotate(-342deg) translateX(-15px) translateY(15px);
    transform: rotate(-342deg) translateX(-15px) translateY(15px);
  }

  100% {
    -webkit-transform: rotate(-360deg) translateX(0px) translateY(0px);
    transform: rotate(-360deg) translateX(0px) translateY(0px);
  }
}

@keyframes mySport {
  0% {
    -webkit-transform: translate(0px, 0px);
    transform: translate(0px, 0px);
  }

  20% {
    -webkit-transform: translate(500px, -440px);
    transform: translate(500px, -440px);
  }

  100% {
    -webkit-transform: translate(500px, -440px);
    transform: translate(500px, -440px);
  }
}

@keyframes mymove {
  10% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }

  20% {
    transform: translateX(-3px) rotate(-2deg);
    -webkit-transform: translateX(-3px) rotate(-2deg);
  }

  30% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }

  40% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }

  50% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }

  60% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }

  70% {
    -webkit-transform: translateX(2px) rotate(1deg);
    transform: translateX(2px) rotate(1deg);
  }

  80% {
    -webkit-transform: translateX(-2px) rotate(-1deg);
    transform: translateX(-2px) rotate(-1deg);
  }

  90% {
    -webkit-transform: translateX(1px) rotate(0);
    transform: translateX(1px) rotate(0);
  }

  100% {
    -webkit-transform: translateX(-1px) rotate(0);
    transform: translateX(-1px) rotate(0);
  }
}

@keyframes myGame {
  0% {
    opacity: 1;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-filter: blur(0px);
    filter: blur(0px);
  }

  50% {
    opacity: 1;
    -webkit-transform-origin: 200% 50%;
    transform-origin: 200% 50%;
    -webkit-transform: rotate(160deg);
    transform: rotate(160deg);
    -webkit-filter: blur(0px);
    filter: blur(0px);
  }

  100% {
    opacity: 0;
    -webkit-transform-origin: 200% 50%;
    transform-origin: 200% 50%;
    -webkit-transform: rotate(160deg);
    transform: rotate(160deg);
    -webkit-filter: blur(20px);
    filter: blur(20px);
  }
}

.hobbiesContStyle {
  grid-area: img;
}

.hobTitleContStyle {
  position: relative;
  height: 2em;
  grid-area: description;
}

.hobbiesImgStyleMove {
  -webkit-animation: mymove linear 3s;
  /* Safari 4.0 - 8.0 */
  animation: mymove linear 3s;
}

.hobbiesAirplaneStyleMove {
  -webkit-animation: myAirplane ease-in-out 2s;
  /* Safari 4.0 - 8.0 */
  animation: myAirplane ease-in-out 2s;
}

.hobbiesSportStyleMove {
  -webkit-animation: mySport ease-in-out 1s;
  /* Safari 4.0 - 8.0 */
  animation: mySport ease-in-out 1s;
}

.hobbiesGameStyleMove {
  -webkit-animation: myGame ease-in-out 1s;
  /* Safari 4.0 - 8.0 */
  animation: myGame ease-in-out 1s;
}

@media only screen and (min-width: 850px) {
  .hobTitleStyle {
    margin: 0;
    font-size: 1.5em;
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
  }

  .hobbiesContainer {
    height: 12em;
  }

  .hobbiesImgStyle {
    margin: 1em 2em 1em 2em;
    height: 4em;
    width: 4em;
  }

}

@media only screen and (max-width: 849px) {
  .hobTitleStyle {
    margin: 0;
    font-size: 1.2em;
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
  }

  .hobbiesContainer {
    height: auto;
  }

  .hobbiesImgStyle {
    margin: 1em .6em 1em .6em;
    height: 2.5em;
    width: 2.5em;
  }

}