@import "../../index.scss";

@media only screen and (min-width: 850px) {
  .cardProfile {
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
    height: 38em;
    margin: 3em 15%;
    background-color: rgb(255, 255, 255);
    border-radius: 1em 1em;
    box-shadow: 1em 1em 6em 0em $boxShadowColor;
    display: grid;
    grid-template-columns: 45% 55%;
    grid-template-rows: 85% 15%;
    grid-template-areas:
      "photo info"
      "media media";
  }

  .photoStyle {
    grid-area: photo;
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-radius: 1em 0em 0em 0em;
  }

  .infoStyle {
    grid-area: info;
    background-color: rgb(255, 255, 255);
    border-radius: 0em 1em 0em 0em;
  }

  .mediaStyle {
    grid-area: media;
    background-color: $baseColor;
    border-radius: 0em 0em 1em 1em;
    text-align: center;
  }

  .imgStyle {
    border-radius: 1em 1em;
    height: 25em;
    margin-top: 14%;
  }

  .iconsStyle {
    height: 2.5em;
    width: 2.5em;
    margin: 1.8em 1em 0em 1em;
    fill: white;
  }

  .iconsStyle:hover {
    fill: $hoverColor;
    transform: scale(1.2);
  }

  .positionStyle {
    font-size: 1.1em;
    margin: 0em 0em 1em 0em;
  }

  .hrStyle {
    border: solid rgb(230, 230, 230) 1px !important;
    width: auto;
    margin: 2em 5em 2em 0em;
  }

  .infoContainer {
    display: grid;
    grid-template-columns: 40% 60%;
    grid-template-areas:
      "titles data"
  }

  .titlesStyle {
    grid-area: titles;
    font-weight: bold;
  }

  .dataStyle {
    grid-area: data;
    color: rgb(130, 130, 130);
  }

  .basicInfoStyle {
    margin: 0em 1em 1em 0em;
  }

  .nameStyle {
    font-size: 2.5em;
    margin: 1.5em 0em 0.1em 0em;
  }
}

@media only screen and (max-width: 849px) {
  .cardProfile {
    background-color: rgb(255, 255, 255);
    box-shadow: 1em 1em 6em 0em $boxShadowColor;
    display: grid;
    grid-template-columns: 100%;
    grid-template-areas: "photo""info""media";
  }

  .photoStyle {
    grid-area: photo;
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-radius: 1em 1em 1em 1em;
  }

  .infoStyle {
    text-align: center;
    grid-area: info;
    background-color: rgb(255, 255, 255);
  }

  .mediaStyle {
    padding: 1em;
    grid-area: media;
    background-color: $baseColor;
    text-align: center;
  }

  .imgStyle {
    border-radius: 1em 1em;
    height: 20em;
    margin-top: 10%;
  }

  .iconsStyle {
    height: 2.5em;
    width: 2.5em;
    margin: 0.5em 1.5em 0em 1.5em;
    fill: white;
    transition: all .3s ease-in-out;
  }

  .iconsStyle:hover {
    fill: $hoverColor;
    transform: scale(1.2);
  }

  .positionStyle {
    font-size: .8em;
    margin: 0em 0em 1em 0em;
  }

  .hrStyle {
    border: solid rgb(230, 230, 230) 1px !important;
    width: auto;
    margin: 2em 2em 2em 2em;
  }

  .infoContainer {
    font-size: 0.8em;
    display: grid;
    grid-template-columns: 45% 55%;
    grid-template-areas:
      "titles data"
  }

  .titlesStyle {
    grid-area: titles;
    font-weight: bold;
    text-align: left;
    padding-left: 2em;
  }

  .dataStyle {
    text-align: left;
    grid-area: data;
    color: rgb(130, 130, 130);
  }

  .basicInfoStyle {
    margin: 0em 1em 1.5em 0em;
  }

  .nameStyle {
    font-size: 2em;
    margin: 1em 0em 0.1em 0em;
  }
}