@import "../index.scss";

.moreStyle {
  grid-area: more;
  text-align: center;
}

.downloadStyle {
  padding: 1.5em 4em;
  background-color: white;
  border-color: $grayColor;
  outline: none;
  font-size: 0.8em;
  border-radius: 1em;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  color: black;
  display: inline-block;
}

.homeStyle {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  display: grid;
}

.downloadStyle:hover {
  background-color: $hoverColor;
  outline: none;
}

.recentWorkStyle {
  white-space: pre-wrap;
  text-align: justify;
}

.hobbiesTitleStyle,
.skillsTitleStyle {
  font-weight: 700;
}

@media only screen and (min-width: 850px) {
  .hobbiesTitleStyle {
    position: relative;
    font-size: 2em;
    margin: 4em auto 1em;
  }
  
  .homeStyle {
    grid-template-rows: 30em auto;
    grid-template-areas: "photo" "more";
  }
  
  .hrStyleHome {
    border: 1px solid $grayColor !important;
    width: auto;
    margin: 0 15em 3em;
  }
  
  .bgPhotoStyle {
    grid-area: photo;
    width: 100%;
    height: 100%;
  }
  
  .downloadStyle {
    font-size: 1em;
    margin: 15em 0 5em;
  }
  
  .recentWorkStyle {
    margin: 0 15% 5em;
    font-size: 1.5em;
  }
  
  .skillsTitleStyle {
    font-size: 2em;
    margin: 2em auto 1em;
  }
  
  .skillsStyle {
    margin: auto;
    transition: all 0.3s ease-in-out;
  }
  
  .skillsStyle:hover {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  
  .imgBoxSize{
    position: relative;
    width: 6em;
    height: 4em;
  }
  
  .imgSkillStyle {
    position: absolute;
    bottom: .4em;
    left: calc(50% - 2em);
    width: 4em;
  }
  
  .nameSkillStyle {
    margin: 0 0 0.3em;
  }
  
  .skillsContainer {
    margin: 0 15em 5em;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-row-gap: 2em;
    grid-column-gap: 1em;
  }
  
  .villaSimpson {
    height: 6em;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    position: absolute;
    z-index: -1;
    top: -3em;
    left: 2em;
  }
  
}

@media only screen and (max-width: 849px) {
  .skillsTitleStyle {
    margin: auto auto 1em;
  }
  
  .homeStyle {
    grid-template-rows: 3% auto auto;
    grid-template-areas: "photo" "principal" "more";
  }
  
  .hrStyleHome {
    border: 1px solid $grayColor !important;
    width: auto;
    margin: 0 4em 3em;
  }
  
  .bgPhotoStyle {
    grid-area: photo;
    width: 100%;
  }
  
  .positionCardProf {
    grid-area: principal;
  }
  
  .downloadStyle {
    font-size: 1em;
    margin: 3em 0;
  }
  
  .recentWorkStyle {
    margin: 0 10% 3em;
    font-size: 1.1em;
  }
  
  .skillsTitleStyle {
    font-size: 2em;
    position: relative;
  }
  
  .skillsStyle {
    margin: auto;
    transition: all 0.3s ease-in-out;
  }
  
  .skillsStyle:hover {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  
  .imgBoxSize{
    position: relative;
    width: 6em;
    height: 4em;
  }
  
  .imgSkillStyle {
    position: absolute;
    left: calc(50% - 2em);
    bottom: .4em;
    width: 4em;
  }
  
  .nameSkillStyle {
    font-size: 1.3em;
    margin: 0 0 0.3em;
  }
  
  .skillsContainer {
    margin: 0 5% 50%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-row-gap: 3%;
    grid-column-gap: 0;
  }
  
  .villaSimpson {
    position: absolute;
    height: 5em;
    right: 0.5em;
    z-index: -1;
  }
  
}

