@media only screen and (min-width: 850px) {
  .cardProfile {
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
    height: 38em;
    margin: 3em 7%;
    background-color: white;
    border-radius: 1em 1em;
    box-shadow: 1em 1em 6em 0 $boxShadowColor;
    display: grid;
    grid-template-columns: 45% 55%;
    grid-template-rows: 85% 15%;
    grid-template-areas: "photo info" "media media";
  }
  
  .photoStyle {
    grid-area: photo;
    text-align: center;
    background-color: white;
    border-radius: 1em 0 0 0;
  }
  
  .infoStyle {
    grid-area: info;
    background-color: white;
    border-radius: 0 1em 0 0;
  }
  
  .mediaStyle {
    grid-area: media;
    background-color: $baseColor;
    border-radius: 0 0 1em 1em;
    text-align: center;
  }
  
  .imgStyle {
    border-radius: 1em 1em;
    height: 25em;
    margin-top: 14%;
  }
  
  .iconsStyle {
    height: 2.5em;
    width: 2.5em;
    margin: 1.8em 1em 0;
    fill: white;
  }
  
  .iconsStyle:hover {
    fill: $hoverColor;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  
  .positionStyle {
    font-size: 1.1em;
    margin: 0 0 1em;
  }
  
  .hrStyle {
    border: 1px solid $hrBorderColor !important;
    width: auto;
    margin: 2em 5em 2em 0;
  }
  
  .infoContainer {
    display: grid;
    grid-template-columns: 40% 60%;
    grid-template-areas: "titles data";
  }
  
  .titlesStyle {
    grid-area: titles;
    font-weight: 700;
  }
  
  .dataStyle,
  .titlesStyle {
  }
  
  .dataStyle {
    grid-area: data;
    color: $grayColor;
  }
  
  .basicInfoStyle {
    margin: 0 1em 1em 0;
  }
  
}

@media only screen and (max-width: 849px) {
  .cardProfile {
    background-color: white;
    box-shadow: 1em 1em 6em 0 $boxShadowColor;
    display: grid;
    grid-template-columns: 100%;
    grid-template-areas: "photo" "info" "media";
  }
  
  .photoStyle {
    grid-area: photo;
    border-radius: 1em 1em 1em 1em;
  }
  
  .infoStyle,
  .photoStyle {
    text-align: center;
    background-color: white;
  }
  
  .infoStyle {
    grid-area: info;
  }
  
  .mediaStyle {
    padding: 1em;
    grid-area: media;
    background-color: $baseColor;
    text-align: center;
  }
  
  .imgStyle {
    border-radius: 1em 1em;
    height: 20em;
    margin-top: 10%;
  }
  
  .iconsStyle {
    height: 2.5em;
    width: 2.5em;
    margin: 0.5em 1.5em 0;
    fill: white;
    transition: all 0.3s ease-in-out;
  }
  
  .iconsStyle:hover {
    fill: $hoverColor;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  
  .positionStyle {
    font-size: 0.8em;
    margin: 0 0 1em;
  }
  
  .hrStyle {
    border: 1px solid $hrBorderColor !important;
    width: auto;
    margin: 2em;
  }
  
  .infoContainer {
    font-size: 0.8em;
    display: grid;
    grid-template-columns: 45% 55%;
    grid-template-areas: "titles data";
  }
  
  .titlesStyle {
    grid-area: titles;
    font-weight: 700;
    padding-left: 2em;
  }
  
  .dataStyle,
  .titlesStyle {
    text-align: left;
  }
  
  .dataStyle {
    grid-area: data;
    color: $grayColor;
  }
  
  .basicInfoStyle {
    margin: 0 1em 1.5em 0;
  }
  
}

